@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/all.min.css);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
}

.share-icon{width:2em;height:2em;border-radius:50%;display:inline-block;margin-right:1em;position:relative;cursor:pointer;transition:ease-in-out 0.5s all}.share-icon:hover{transform:scale(1.1)}.share-icon .social-icon{color:white;font-size:1em;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}


#new-quote{border:none;padding:.6em;border-radius:0.5em;font-size:1em;color:white;font-weight:bold;cursor:pointer;transition:ease-in-out 0.5s all}#new-quote:hover{transform:scale(1.05)}

#quote-box{width:90vw;min-height:250px;background-color:white;border-radius:2em;padding:2em;transition:ease-in-out .5s all}.item-flex{display:flex;justify-content:space-between}.item-flex #text{font-size:1.5em;margin:1em 0}.item-flex #author{font-size:1em;font-style:italic}.item-flex .quote-icon{font-size:2em}@media screen and (min-width: 700px){#quote-box{width:43vw}}

.App{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: ease-in-out .5s all;
}

footer{
    margin-top: 2em;
}

footer p {
    color: white;
    font-size: 0.9em;
}
